$('.header').each((index, component) => {
	let $component = $(component);
	let $mobileMenuBtn = $(component).find('.js-mobile-menu-btn');

	$(window).on('fullpage:scrollInner', () => {
		$component.stop(true, true).addClass('header_fullpage-active');
	});

	$(window).on('fullpage:scrollToTop', () => {
		$component.stop(true, true).removeClass('header_fullpage-active');
	});

	$mobileMenuBtn.on('click', (e) => {
		e.preventDefault();

		let $target = $(e.target);

		$target.stop(true, true).toggleClass('active');

		if ($target.hasClass('active')) {
			$(window).trigger('mobile-menu:open');

			if (!$component.hasClass('header_index') && !$component.hasClass('header__open-menu') && !$component.hasClass('active'))
				$component.stop(true, true).addClass('header__open-menu');

		} else {
			$(window).trigger('mobile-menu:close');

			if (!$component.hasClass('header_index') && $component.hasClass('header__open-menu') && !$component.hasClass('active'))
				$component.stop(true, true).removeClass('header__open-menu');
		}
	});

	if ($(window).innerWidth() < 1025) {
		$(window).on('scroll', () => {
			if ($(window).scrollTop() > $component.outerHeight() / 2) {
				if (!$component.hasClass('active')) {
					$component.stop(true, true).addClass('active');
					$('.cart').stop(true, true).addClass('active');
					if (!$component.hasClass('header_index') && !$component.hasClass('header__open-menu'))
						$component.addClass('header__open-menu');
				}
			} else {
				if ($component.hasClass('active')) {
					$component.stop(true, true).removeClass('active');
					$('.cart').stop(true, true).removeClass('active');
					if (!$component.hasClass('header_index') && $component.hasClass('header__open-menu')  && !$component.hasClass('active'))
						$component.removeClass('header__open-menu');
				}
			}
		});
	}
});



